
.ratingselect .rating-type[data-v-7c664482] {
  padding: 18px 0;
  margin: 0 18px;
  position: relative;
  font-size: 0;
}
.ratingselect .rating-type[data-v-7c664482]:after {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    content: "";
    background: rgba(7, 17, 27, 0.1);
}
.ratingselect .rating-type .block[data-v-7c664482] {
    display: inline-block;
    padding: 8px 12px;
