				&.active{
          color: #fff;
				}
*/
}
.ratingselect .rating-type .block.positive[data-v-7c664482] {
      background-color: rgba(0, 160, 220, 0.2);
}
.ratingselect .rating-type .block.positive.active[data-v-7c664482] {
        color: #fff;
        background-color: #00a0dc;
}
.ratingselect .rating-type .block.negative[data-v-7c664482] {
      background-color: rgba(77, 85, 93, 0.2);
}
.ratingselect .rating-type .block.negative.active[data-v-7c664482] {
        color: #fff;
        background-color: #4d555d;
}
.ratingselect .rating-type .block .count[data-v-7c664482] {
      margin-left: 2px;
      font-size: 8px;
}
.ratingselect .switch[data-v-7c664482] {
  padding: 12px 18px;
  line-height: 24px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
  color: #93999f;
  font-size: 0;
}
.ratingselect .switch span[data-v-7c664482] {
    display: inline-block;
    vertical-align: top;
}
.ratingselect .switch .icon[data-v-7c664482] {
    margin-right: 4px;
    font-size: 24px;
}
.ratingselect .switch .text[data-v-7c664482] {
    font-size: 12px;
}
.ratingselect .switch.on .icon[data-v-7c664482] {
    color: #00c850;
}



